"use strict";
document.addEventListener("DOMContentLoaded", () => {
  const body = document.body,
    burgerBtn = document.querySelector(".btn-burger"),
    burgerMenu = document.querySelector(".header-info"),
    closeBurgerMenuBtn = burgerMenu.querySelector(".header-btn--close");
  const overlay = document.querySelector(".overlay_body");
  burgerBtn.addEventListener("click", () => {
    burgerMenu.classList.add("active");
  });
  closeBurgerMenuBtn.addEventListener("click", () => {
    burgerMenu.classList.remove("active");
  });
  //Все для автоскролла в секции услуги
  const servicesList = document.querySelector(".services-list");
  const items = servicesList.querySelectorAll(".item");
  if (document.body.clientWidth > 1023) {
    let itemsWidth = 0;
    for (let i = 0; i < items.length; i++) {
      itemsWidth += parseInt(items[i].clientWidth);
    }

    const servicesWrapper = document.querySelector(".services-list--overflow");
    servicesWrapper.style.cssText = `width:${document.body.clientWidth}px; 
    min-width: ${document.body.clientWidth}px; overflow-x: hidden`;
    new Vue({
      el: "#app_vue",
      data() {
        return {
          sectionInViewport: false,
          element: document.querySelector(".services-list"),
          cnt: 0,
          styleObject: "",
          isScroll: false,
          singleItemWidth: itemsWidth / items.length,
          fullItemsWidth: itemsWidth - items[0].clientWidth,
          persentItemWidth: Math.floor(
            ((items[0].clientWidth + 16) / (itemsWidth + 16 * items.length)) *
              100 -
              3
          ),

          maxPersentTransparent: 100,
        };
      },
      mounted() {
        const options = {
          root: null, // Используем viewport как корневой элемент
          rootMargin: "0px", // Может быть настроено для марджинов
          threshold: 0.7, // Порог видимости (от 0 до 1)
        };
        const observer = new IntersectionObserver(
          this.handleIntersection,
          options
        );
        observer.observe(this.$refs.sectionToTrack);
        document.body.clientWidth > 767
          ? document.addEventListener("wheel", this.onScroll)
          : "";
      },
      methods: {
        handleIntersection(entries) {
          // Вызывается при изменении видимости
          if (entries[0].isIntersecting) {
            this.sectionInViewport = true;
            // Секция находится в зоне видимости
            console.log(this.sectionInViewport);
          } else {
            this.sectionInViewport = false;
            // Секция не находится в зоне видимости
            console.log(this.sectionInViewport);
          }
        },
        calcMaxDown() {},
        onScroll(event) {
          event = event || window.event;

          let y = event.deltaY || event.detail || event.wheelDelta,
            val = 0.1,
            min = 0,
            max = 100;
          if (
            y > 0 &&
            this.sectionInViewport &&
            -this.cnt < this.fullItemsWidth
          ) {
            this.cnt = this.cnt - this.singleItemWidth;
            body.classList.add("horizontal");
            this.styleObject = "transform: translate(" + this.cnt + "px)";
          }
          if (
            y < 0 &&
            this.sectionInViewport &&
            (this.ctn >= 0 || this.cnt != 0)
          ) {
            this.cnt = this.cnt + this.singleItemWidth;

            console.log("up");
            this.styleObject = "transform: translate(" + this.cnt + "px)";
          } else {
            body.classList.remove("horizontal");
          }
        },
      },
      watch: {
        cnt: function (newVal, oldVal) {
          newVal == 0 ? body.classList.remove("horizontal") : "";
          newVal == -190
            ? body.classList.remove("horizontal")
            : body.classList.add("horizontal");
        },
      },
    });
  } else {
    servicesList.classList.add("owl-carousel", "owl-theme");
    const servicePopup = document.querySelector(".service_popup"),
      servicePopupClose = servicePopup.querySelector(".close_btn");
    $(".services-list").owlCarousel({
      loop: true,
      margin: 10,
      nav: false,
      dots: false,
      responsive: {
        0: {
          items: 1.2,
        },
        600: {
          items: 2.2,
        },
      },
    });
    items.forEach((i) => {
      i.addEventListener("click", () => {
        let title = i.querySelector("h2"),
          descr = i.querySelector(".item-descr"),
          img = i.querySelector(".img img");
        servicePopup.querySelector(".service_popup-title").textContent =
          title.textContent;
        servicePopup.querySelector(
          ".service_popup-top"
        ).style.cssText = `background: url(${img.src})center/cover no-repeat`;
        servicePopup.querySelector(".service_popup-body").innerHTML =
          descr.innerHTML;
        servicePopup.classList.add("active");
        overlay.classList.add("active");
        body.style.overflow = "hidden";
      });
    });
    servicePopupClose.addEventListener("click", () => {
      servicePopup.classList.remove("active");
      overlay.classList.remove("active");
      body.style.overflow = "";
    });
  }
});
